@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

.slider-wr{
  position: relative;
}
.currentSlide{
  position: absolute;
  top: 20px;
  left: 20px;
  z-index: 101;
}

.rslider{
  position: relative;
  z-index: 100;
  
  background-image: url(./images/test.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}
  .slick-slide{
    display: -ms-flexbox !important;
    display: flex !important;
    -ms-flex-align: center;
        align-items: center;

    height: 100vh;
  }
  .slick-slide.slick-active.slick-current .player{
    background-color: var(--primary-color);
    color: var(--night);
    -webkit-filter: blur(0) grayscale(0);
            filter: blur(0) grayscale(0);
  }
  .slick-slide.slick-active.slick-current .player__image--front{
    opacity: .3;
    z-index: 2;
  }
  .slick-slide.slick-active.slick-current .player__image--back{
    opacity: 1;
    z-index: 3;
  }

  /* slick dots */
  .slick-dots{
    bottom: auto;
    top: 20px;
    right: 20px;
    text-align: right;
  }
    .slick-dots li{
      width: 30px;
      height: auto;
    }
    .slick-dots .button{
      text-align: center;
      background-color: var(--primary-color);
    
      -webkit-transition: color .25s ease-in;
    
      -o-transition: color .25s ease-in;
    
      transition: color .25s ease-in;
    }
      .slick-dots .button:hover{
        color: var(--secondary-color);
      }
    .slick-dots .slick-active .button{
      color: var(--secondary-color);
      background-color: var(--snow);
    }

.player{
  width: 300px;
  height: 70vh;
  margin-left: 15px;
  margin-right: 15px;
  padding: 10px;
  position: relative;
  
  display: -ms-flexbox;
  
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-direction: column;
      flex-direction: column;
  
  font-size: 2rem;
  color: var(--snow);
  -webkit-filter: blur(1px) grayscale(100%);
          filter: blur(1px) grayscale(100%);

  -webkit-transition: background-color .25s ease-in-out;

  -o-transition: background-color .25s ease-in-out;

  transition: background-color .25s ease-in-out;
}
  .player:hover{
    -webkit-filter: blur(0) grayscale(0);
            filter: blur(0) grayscale(0);
    background-color: var(--snow);
    z-index: 10;
    color: var(--secondary-color);
  }
  .player__flex{
    margin: 0;
    position: relative;
    z-index: 5;

    display: -ms-flexbox;

    display: flex;
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-pack: justify;
        justify-content: space-between;
  }
    .player__flex i {
      font-family: Helvetica, sans-serif;
    }
  .player__team{
    font-size: 15px;
  }
  .player__name{
  }
  .player__nickname{
    -webkit-writing-mode: vertical-lr;
        -ms-writing-mode: tb-lr;
            writing-mode: vertical-lr;
  }

.player__image-wrapper{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 75%;
}
.player__image{
  height: 100%;
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  -o-transition: transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  position: relative;
} 
  .player__image--front,
  .player__image--back{
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transition: .25s ease-in-out;
    -o-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
  }
  .player__image--front{
    opacity: 1;
    z-index: 2;
  }
    .player__image--front img{
      display: inline-block;
      width: 100%;
      height: 100%;
      -o-object-fit: contain;
         object-fit: contain;
    }
  .player__image--back{
    opacity: .3;
    z-index: 1;
    text-align: center;
  }
    .player__image--back img{
      display: inline-block;
      width: 90%;
      height: 100%;
      -o-object-fit: contain;
         object-fit: contain;
    }



